import request from '@/utils/request'

const baseUrl = '/api/admin/category'

const categoryApi = {}

categoryApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

categoryApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

categoryApi.get = categoryId => {
  return request({
    url: `${baseUrl}/${categoryId}`,
    method: 'get'
  })
}

categoryApi.tree = () => {
  return request({
    url: `${baseUrl}/tree`,
    method: 'get'
  })
}

categoryApi.create = category => {
  return request({
    url: `${baseUrl}/save`,
    data: category,
    method: 'post'
  })
}

categoryApi.update = (category) => {
  return request({
    url: `${baseUrl}/update`,
    data: category,
    method: 'put'
  })
}

categoryApi.delete = categoryId => {
  return request({
    url: `${baseUrl}/${categoryId}`,
    method: 'delete'
  })
}

function concreteTree(parentCategory, categories) {
  categories.forEach(category => {
    if (parentCategory.key === category.parentId) {
      if (!parentCategory.children) {
        parentCategory.children = []
      }
      parentCategory.children.push({
        key: category.id,
        title: category.name,
        isLeaf: false
      })
    }
  })

  if (parentCategory.children) {
    parentCategory.children.forEach(category => concreteTree(category, categories))
  } else {
    parentCategory.isLeaf = true
  }
}

categoryApi.concreteTree = categories => {
  const topCategoryNode = {
    key: 0,
    title: 'top',
    children: []
  }
  concreteTree(topCategoryNode, categories)
  return topCategoryNode.children
}

export default categoryApi
