import request from '@/utils/request'

const baseUrl = '/api/admin/content'

const contentApi = {}

contentApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

contentApi.page = (params) => {
  return request({
    url: `${baseUrl}/page-plus`,
    params: params,
    method: 'get'
  })
}

contentApi.get = contentId => {
  return request({
    url: `${baseUrl}/${contentId}`,
    method: 'get'
  })
}

contentApi.update = (params) => {
  return request({
    url: `${baseUrl}/update`,
    data: params,
    method: 'put'
  })
}

contentApi.create = content => {
  return request({
    url: `${baseUrl}/save`,
    data: content,
    method: 'post'
  })
}

contentApi.delete = contentId => {
  return request({
    url: `${baseUrl}/${contentId}`,
    method: 'delete'
  })
}

export default contentApi
