<template>
  <el-pagination
    :background="false"
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="pageNum"
    :page-sizes="[5,10, 20, 30, 40, 50]"
    :page-size="pageSize"
    :layout="optionsArr.join(',')"
    :total="total"
  ></el-pagination>
</template>

<script>
export default {
  props: {
    pageSize: {
      default: 10
    },
    pageNum: {
      default: 1
    },
    total: {
      default: 0
    }
  },
  data() {
    return {
      optionsArr: ["total", "sizes", "prev", "pager", "next", "jumper"]
    };
  },
  methods: {
    handleSizeChange(val) {
      this.$emit("sizeChange", val);
    },
    handleCurrentChange(val) {
      this.$emit("currentChange", val);
    }
  }
};
</script>

