<template>
  <el-tree
    ref="categoryTree"
    :highlight-current="true"
    :data="categories"
    :props="props"
    @node-click="handleNodeClick"
  ></el-tree>
</template>

<script>
import categoryApi from "@/api/categoryApi";
export default {
  created() {
    this.getCategoryTree();
  },
  data() {
    return {
      categories: [],
      props: {
        children: "children",
        label: "name"
      }
    };
  },
  methods: {
    getCategoryTree() {
      categoryApi.tree().then(res => {
        this.categories = res.data;
        this.categories.unshift({ id: null, name: "全部" });
      });
    },
    handleNodeClick(data, node, nodeComponent) {
      this.$emit("node-click", data);
    }
  }
};
</script>

