<template>
  <div>
    <el-row :gutter="20" style="display:flex;">
      <el-col class="mb20" :xl="5" :lg="6" :md="10" :sm="24" :xs="24">
        <el-card style="height:100%;">
          <div slot="header" class="clearfix">
            <span>选择栏目</span>
          </div>
          <category-tree @node-click="handleNodeClick"></category-tree>
        </el-card>
      </el-col>
      <el-col class="mb20" :xl="19" :lg="18" :md="14" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>内容列表</span>
          </div>
          <div>
            <el-form :inline="true">
              <el-form-item label="内容标题">
                <el-input v-model="queryParams.title" placeholder="内容标题"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getContents()">查询</el-button>
              </el-form-item>
              <el-form-item v-if="addPermission">
                <el-button type="primary" @click="$router.push('/content/write')">新增</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div style="padding:20px;">
            <el-table
              :data="contents"
              style="margin-bottom:20px;"
              v-loading="tableLoading"
              element-loading-background="rgba(0, 0, 0, 0.3)"
              element-loading-text="数据正在加载中"
              element-loading-spinner="el-icon-loading"
            >
              <el-table-column prop="id" label="ID" width="60"></el-table-column>
              <el-table-column prop="title" label="标题" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-link
                    :underline="false"
                    :href="'/archives/'+scope.row.path"
                    target="_blank"
                    type="primary"
                  >{{ scope.row.title }}</el-link>
                </template>
              </el-table-column>
              <el-table-column prop="status" label="状态" width="90" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-tag v-if="scope.row.status == 1" type="success">已发布</el-tag>
                  <el-tag v-if="scope.row.status == 0" type="warning">草稿</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="categoryNames" label="栏目" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="path" label="路径" width="150"></el-table-column>
              <el-table-column prop="visits" label="访问" width="80"></el-table-column>
              <el-table-column prop="likes" label="喜欢" width="80"></el-table-column>
              <el-table-column prop="createTime" label="创建时间" width="180"></el-table-column>
              <el-table-column label="操作" width="150">
                <template slot-scope="scope">
                  <!-- <el-button type="text" @click="edit(scope.row)">设置</el-button> -->
                  <el-button v-if="editPermission" type="text" @click="edit(scope.row)">编辑</el-button>
                  <el-popconfirm
                    v-if="delPermission"
                    title="确定删除本条数据吗？删除后不可恢复。"
                    cancelButtonType="default"
                    style="margin-left:10px;"
                    @onConfirm="del(scope.row)"
                  >
                    <el-button type="text" slot="reference">删除</el-button>
                  </el-popconfirm>
                </template>
              </el-table-column>
            </el-table>
            <pagination
              @sizeChange="handleSizeChange"
              @currentChange="handleCurrentChange"
              :pageNum="queryParams.pageNum"
              :pageSize="queryParams.pageSize"
              :total="total"
            ></pagination>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import CategoryTree from "@/views/components/category-tree";
import Pagination from "@/components/pagination";
import contentApi from "@/api/contentApi";
import store from "@/store";
export default {
  components: {
    CategoryTree,
    Pagination
  },
  data() {
    return {
      addPermission: false,
      editPermission: false,
      delPermission: false,
      tableLoading: false,
      contents: [],
      queryParams: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0
    };
  },
  created() {
    this.addPermission = store.state.user.permissions.indexOf(300101001) > -1;
    this.editPermission = store.state.user.permissions.indexOf(300101002) > -1;
    this.delPermission = store.state.user.permissions.indexOf(300101003) > -1;
    this.getContents();
  },
  methods: {
    getContents() {
      if (this.queryParams.name == "") {
        this.queryParams.name = null;
      }
      this.tableLoading = true;
      contentApi.page(this.queryParams).then(res => {
        this.tableLoading = false;
        this.contents = res.data.records;
        this.total = res.data.total;
      });
    },
    handleNodeClick(node) {
      this.queryParams.categoryId = node.id;
      this.getContents();
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getContents();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getContents();
    },
    edit(row) {
      this.$router.push("/content/write?contentId=" + row.id);
    },
    del(row) {
      contentApi.delete(row.id).then(res => {
        this.$message.success("删除成功");
        this.getContents();
      });
    }
  }
};
</script>

